*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#html {
  word-break: break-all;
}
#div1 {
  position: fixed;
  right: 20px;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}
#div1::before {
  content: '';
  display: block;
  position: absolute;
}
#div1::after {
  content: '';
  display: block;
  position: absolute;
}
@media (max-width: 500px) {
  #html{
    height: 50vh;
    overflow: auto;
  }
  #div1Wrapper{
    height: 50vh;
  }
  #div1{
    position: relative;
    top: 0;
    right: 0;
  }
}